/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Calls Page Styles */
.calls-page {
  min-height: 100vh;
  background: #ffffff;
  padding: 0;
  position: relative;
}

.calls-content {
  margin: 0 auto;
  padding: 15px 10px;
}

.calls-header {
  text-align: center;
  margin-bottom: 30px;
}

.calls-title {
  font-size: 2rem;
  font-weight: 700;
  color: #333;
  margin-bottom: 8px;
}

.calls-subtitle {
  color: #666;
  font-size: 1rem;
  font-weight: 400;
}

.call-options {
  margin-bottom: 40px;
}

.call-option-card {
  background: #ffffff;
  border: 2px solid #e0e0e0;
  border-radius: 16px;
  padding: 20px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.call-option-card:hover {
  transform: translateY(-2px);
  border-color: #667eea;
  box-shadow: 0 8px 30px rgba(102, 126, 234, 0.2);
}

.call-icon {
  width: 50px;
  height: 50px;
  background: #f5f5f5;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
  color: #667eea;
}

.call-info {
  flex: 1;
}

.call-info h3 {
  color: #333;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 4px;
}

.call-info p {
  color: #666;
  font-size: 0.9rem;
}

.call-arrow {
  color: #999;
  transition: all 0.3s ease;
}

.call-option-card:hover .call-arrow {
  color: #667eea;
  transform: translateX(4px);
}

.recent-calls-section {
  background: #f8f9fa;
  border-radius: 16px;
  padding: 20px;
  border: 1px solid #e0e0e0;
}

.section-title {
  color: #333;
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 16px;
}

.recent-calls-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.recent-call-item {
  display: flex;
  align-items: center;
  padding: 12px;
  background: #ffffff;
  border-radius: 12px;
  transition: all 0.3s ease;
  border: 1px solid #e0e0e0;
}

.recent-call-item:hover {
  background: #f8f9fa;
  border-color: #667eea;
}

.call-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 12px;
}

.call-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.call-details {
  flex: 1;
}

.call-details h4 {
  color: #333;
  font-size: 0.95rem;
  font-weight: 600;
  margin-bottom: 2px;
}

.call-details p {
  color: #666;
  font-size: 0.8rem;
}

.call-status {
  color: #999;
  width: 24px;
  height: 24px;
}

/* Ads Gallery Reel Styles */
.ads-gallery-reel {
  height: 100vh;
  background: #000;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.reel-progress {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.progress-bar {
  display: flex;
  gap: 4px;
  background: rgba(255, 255, 255, 0.2);
  padding: 4px 8px;
  border-radius: 20px;
  backdrop-filter: blur(10px);
}

.progress-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.4);
  transition: all 0.3s ease;
}

.progress-dot.active {
  background: #fff;
  transform: scale(1.2);
}

.progress-text {
  color: white;
  font-size: 12px;
  font-weight: 500;
  background: rgba(0, 0, 0, 0.5);
  padding: 4px 8px;
  border-radius: 12px;
  backdrop-filter: blur(10px);
}

.reel-content {
  flex: 1;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ad-reel-item {
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  display: flex;
  flex-direction: column;
}

.ad-media-container {
  flex: 1;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.ad-media {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.ad-overlay-gradient {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 200px;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.8));
  pointer-events: none;
}

/* TikTok Style Ad Info */
.ad-info-tiktok {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  color: white;
  z-index: 10;
  pointer-events: none;
}

.tiktok-actions {
  position: absolute;
  right: 20px;
  bottom: 120px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  pointer-events: auto;
}

.action-group {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.action-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.action-item:hover {
  transform: scale(1.1);
}

.action-icon {
  width: 48px;
  height: 48px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.3);
  transition: all 0.3s ease;
}

.action-item:hover .action-icon {
  background: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.5);
}

.action-icon.liked {
  background: rgba(239, 68, 68, 0.8) !important;
  border-color: rgba(239, 68, 68, 0.9) !important;
  transform: scale(1.2);
}

.action-icon.liked svg {
  color: white !important;
}

@keyframes likeAnimation {
  0% {
    background: rgba(255, 255, 255, 0.2);
    border-color: rgba(255, 255, 255, 0.3);
    transform: scale(1);
  }
  50% {
    background: rgba(239, 68, 68, 0.9);
    border-color: rgba(239, 68, 68, 1);
    transform: scale(1.3);
  }
  100% {
    background: rgba(239, 68, 68, 0.8);
    border-color: rgba(239, 68, 68, 0.9);
    transform: scale(1.2);
  }
}

@keyframes heartBeat {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.2);
  }
  50% {
    transform: scale(1.1);
  }
  75% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.action-icon.liking {
  animation: likeClick 0.3s ease-out;
}

/* Ensure liked state overrides hover states */
.action-item:hover .action-icon.liked {
  background: rgba(239, 68, 68, 0.8) !important;
  border-color: rgba(239, 68, 68, 0.9) !important;
}

@keyframes likeClick {
  0% {
    transform: scale(1);
    background: rgba(255, 255, 255, 0.2);
  }
  50% {
    transform: scale(1.4);
    background: rgba(239, 68, 68, 0.9);
    box-shadow: 0 0 20px rgba(239, 68, 68, 0.6);
  }
  100% {
    transform: scale(1.2);
    background: rgba(239, 68, 68, 0.8);
  }
}

