body {
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  -webkit-font-feature-settings: "kern" 1;
  font-feature-settings: "kern" 1;
  background: #F571A1;
  color: #333;
  overflow-x: hidden;
}

/* Mobile Only Message */
.mobile-only-message {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #000000;
  color: white;
  text-align: center;
  padding: 2rem;
  font-family: 'Poppins', sans-serif;
}

.desktop-logo {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin-bottom: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  border: 3px solid rgba(255, 255, 255, 0.2);
}

.mobile-only-message h2 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.mobile-only-message p {
  font-size: 1.2rem;
  line-height: 1.6;
  opacity: 0.9;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

/* Desktop Styles */
@media (min-width: 768px) {
  .mobile-only-message {
    display: flex;
  }
  
  .App > div:not(.mobile-only-message):not(.admin-panel):not(.admin-login) {
    display: none;
  }
}

/* Hide mobile-only message when admin panel or admin login is active */
.admin-panel ~ .mobile-only-message,
.admin-panel + .mobile-only-message,
.admin-panel .mobile-only-message,
.admin-login ~ .mobile-only-message,
.admin-login + .mobile-only-message,
.admin-login .mobile-only-message {
  display: none !important;
}

/* Additional rules to ensure mobile-only message is hidden when admin components are present */
.App:has(.admin-panel) .mobile-only-message,
.App:has(.admin-login) .mobile-only-message {
  display: none !important;
}

/* Force hide mobile-only message for admin routes */
.mobile-only-message {
  display: none !important;
}

/* Login Page Styles */
.login-container {
  min-height: 100vh;
  display: flex;
  background: linear-gradient(135deg, #ff6b9d 0%, #c44569 100%);
}

.login-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
  align-items: center;
  justify-content: flex-start;
}

/* Top Section - Logo and Brand Name */
.header-section {
  text-align: center;
  margin-bottom: 1.5rem;
  width: 100%;
}

.brand-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
}

.brand-logo {
