/* ========================================
   CALL CONNECTING ANIMATION STYLES
   ======================================== */

.call-connecting-modal .modal-content {
  max-width: 500px;
  text-align: center;
}

.call-connecting-animation {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 1rem 0;
}

/* Phone Icon Container */
.phone-icon-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 180px;
}

.phone-icon {
  width: 90px;
  height: 90px;
  color: #F571A1;
  z-index: 2;
  position: relative;
  animation: phone-bounce 2s ease-in-out infinite;
}

.camera-icon {
  color: #ff6b9d;
  filter: drop-shadow(0 0 10px rgba(255, 107, 157, 0.3));
  animation: camera-bounce 2s ease-in-out infinite;
}

/* Pulse Rings Animation */
.phone-pulse-ring {
  position: absolute;
  border: 4px solid #F571A1;
  border-radius: 50%;
  animation: pulse-ring 2s ease-out infinite;
  opacity: 0;
}

/* Video call specific pulse rings */
.call-connecting-modal.video-call .phone-pulse-ring {
  border-color: #ff6b9d;
  border-width: 5px;
  filter: drop-shadow(0 0 8px rgba(255, 107, 157, 0.4));
}

.phone-pulse-ring:nth-child(2) {
  width: 120px;
  height: 120px;
  animation-delay: 0.3s;
}

.phone-pulse-ring:nth-child(3) {
  width: 150px;
  height: 150px;
  animation-delay: 0.9s;
}

/* Loading Dots */
.loading-dots {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #F571A1;
  animation: dot-bounce 1.4s ease-in-out infinite both;
}

.dot:nth-child(1) { animation-delay: -0.32s; }
.dot:nth-child(2) { animation-delay: -0.16s; }
.dot:nth-child(3) { animation-delay: 0s; }

/* Connection Status */
.connection-status {
  text-align: center;
  max-width: 300px;
}

.status-text {
  font-size: 1.1rem;
  color: #333;
  margin-bottom: 1rem;
  font-weight: 500;
}

.status-indicator {
  width: 100%;
  height: 4px;
  background: rgba(245, 113, 161, 0.2);
  border-radius: 2px;
  overflow: hidden;
}

.status-bar {
  height: 100%;
  background: linear-gradient(90deg, #F571A1, #ff8fab);
  border-radius: 2px;
  animation: status-progress 3s ease-in-out infinite;
}

/* Cancel Button */
.cancel-call-btn {
  background: linear-gradient(135deg, #ff6b6b 0%, #ee5a52 100%);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.3);
}

.cancel-call-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 107, 107, 0.4);
}

/* ========================================
   ANIMATION KEYFRAMES
   ======================================== */

@keyframes phone-bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

@keyframes camera-bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0) scale(1);
  }
  40% {
    transform: translateY(-15px) scale(1.05);
  }
  60% {
    transform: translateY(-8px) scale(1.02);
  }
}

@keyframes pulse-ring {
  0% {
    transform: scale(0.3);
    opacity: 1;
  }
  80%, 100% {
    opacity: 0;
  }
}

@keyframes dot-bounce {
  0%, 80%, 100% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
}

@keyframes status-progress {
  0% {
    width: 0%;
  }
  50% {
    width: 70%;
  }
  100% {
    width: 100%;
  }
}

