  justify-content: center;
  color: #F571A1;
}

.stat-icon svg {
  width: 28px;
  height: 28px;
}

.stat-info h3 {
  font-size: 2rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0 0 0.25rem 0;
}

.stat-info p {
  color: #64748b;
  font-size: 0.9rem;
  margin: 0;
  font-weight: 500;
}

/* Campaigns Section */
.campaigns-section {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(245, 113, 161, 0.1);
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.section-header h2 {
  font-size: 1.75rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0;
}

.tab-buttons {
  display: flex;
  gap: 0.5rem;
  background: #f1f5f9;
  padding: 0.25rem;
  border-radius: 12px;
}

.tab-btn {
  padding: 0.75rem 1.25rem;
  border: none;
  background: transparent;
  color: #64748b;
  font-size: 0.9rem;
  font-weight: 600;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.tab-btn.active {
  background: white;
  color: #F571A1;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.tab-btn:hover:not(.active) {
  color: #475569;
}

/* Campaigns Grid */
.campaigns-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1.5rem;
}

.campaign-card {
  background: white;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(245, 113, 161, 0.1);
  transition: all 0.3s ease;
}

.campaign-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 30px rgba(245, 113, 161, 0.15);
}

.campaign-media {
  position: relative;
  height: 200px;
  overflow: hidden;
}

.campaign-media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.campaign-card:hover .campaign-media img {
  transform: scale(1.05);
}

.media-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #f1f5f9 0%, #e2e8f0 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #94a3b8;
}

.campaign-status {
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.status-badge {
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  color: white;
  text-transform: capitalize;
}

.status-pending {
  background: #f59e0b;
}

.status-approved {
  background: #10b981;
}

.status-rejected {
  background: #ef4444;
}

.status-review {
  background: #3b82f6;
}

.campaign-info {
  padding: 1.5rem;
}

.campaign-info h3 {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0 0 1rem 0;
  line-height: 1.3;
}

.campaign-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.campaign-meta span {
  color: #64748b;
  font-size: 0.85rem;
  font-weight: 500;
}

.campaign-actions {
  display: flex;
  gap: 0.75rem;
}

.action-btn {
  padding: 0.75rem 1.25rem;
  border: none;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  flex: 1;
}

.action-btn:first-child {
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  color: white;
}

.action-btn.secondary {
  background: rgba(245, 113, 161, 0.1);
  color: #F571A1;
  border: 1px solid rgba(245, 113, 161, 0.2);
}

.action-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.15);
}

/* Empty State */
.empty-state {
  text-align: center;
  padding: 3rem 2rem;
  grid-column: 1 / -1;
}

.empty-icon {
  margin-bottom: 1.5rem;
  color: #94a3b8;
}

.empty-state h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0 0 1rem 0;
}

.empty-state p {
  color: #64748b;
  font-size: 1rem;
  margin: 0 0 2rem 0;
}

.create-first-btn {
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(245, 113, 161, 0.3);
}

.create-first-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(245, 113, 161, 0.4);
}

/* Mobile Navigation Tabs */
.mobile-nav-tabs {
  display: none;
  background: white;
  border-radius: 16px;
  padding: 0.5rem;
  margin-bottom: 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(245, 113, 161, 0.1);
  gap: 0.25rem;
}

.mobile-nav-tab {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 0.5rem;
  border: none;
  background: transparent;
  color: #64748b;
  font-size: 0.8rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 12px;
  min-height: 80px;
}

.mobile-nav-tab svg {
  width: 24px;
  height: 24px;
  color: #64748b;
  transition: all 0.3s ease;
}

.mobile-nav-tab.active {
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(245, 113, 161, 0.3);
}

.mobile-nav-tab.active svg {
  color: white;
}

.mobile-nav-tab:hover:not(.active) {
  background: rgba(245, 113, 161, 0.05);
  color: #475569;
}

.mobile-nav-tab:hover:not(.active) svg {
  color: #F571A1;
}

/* Quick Actions */
.quick-actions {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid rgba(245, 113, 161, 0.1);
  margin-bottom: 2rem;
}

.quick-actions h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0 0 1.5rem 0;
  text-align: center;
}

.quick-actions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 1rem;
}

.quick-action-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.75rem;
  padding: 1.5rem 1rem;
  border: none;
  background: linear-gradient(135deg, rgba(245, 113, 161, 0.05) 0%, rgba(255, 143, 171, 0.05) 100%);
  border-radius: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 1px solid rgba(245, 113, 161, 0.1);
  min-height: 120px;
}

.quick-action-btn:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 25px rgba(245, 113, 161, 0.15);
  border-color: rgba(245, 113, 161, 0.3);
  background: linear-gradient(135deg, rgba(245, 113, 161, 0.1) 0%, rgba(255, 143, 171, 0.1) 100%);
}

.quick-action-icon {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  background: linear-gradient(135deg, rgba(245, 113, 161, 0.1) 0%, rgba(255, 143, 171, 0.1) 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #F571A1;
  transition: all 0.3s ease;
}

.quick-action-btn:hover .quick-action-icon {
  background: linear-gradient(135deg, rgba(245, 113, 161, 0.2) 0%, rgba(255, 143, 171, 0.2) 100%);
  transform: scale(1.1);
}

.quick-action-btn span {
  font-size: 0.85rem;
  font-weight: 600;
  color: #374151;
  text-align: center;
  line-height: 1.3;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .advertiser-sidebar {
    width: 240px;
  }
  
  .advertiser-main {
    margin-left: 240px;
  }
  
  .stats-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
  
  .campaigns-grid {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
}

@media (max-width: 768px) {
  .advertiser-sidebar {
    transform: translateX(-100%);
    transition: transform 0.3s ease;
  }
  
  .advertiser-sidebar.open {
    transform: translateX(0);
  }
  
  .advertiser-main {
    margin-left: 0;
  }
  
  .advertiser-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  
  .campaigns-grid {
    grid-template-columns: 1fr;
  }
  
  .section-header {
    flex-direction: column;
    align-items: stretch;
  }
  
  /* Show mobile nav tabs on mobile */
  .mobile-nav-tabs {
    display: flex;
  }
  
  /* Hide sidebar on mobile */
  .advertiser-sidebar {
    display: none;
  }
  
  /* Adjust quick actions for mobile */
  .quick-actions-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 0.75rem;
  }
  
  .quick-action-btn {
    padding: 1rem 0.5rem;
    min-height: 100px;
  }
  
  .quick-action-btn span {
    font-size: 0.75rem;
  }
  
  .quick-action-icon {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 480px) {
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .quick-actions-grid {
    grid-template-columns: 1fr;
  }
  
  .dashboard-content {
    padding: 1rem;
  }
  
  .campaigns-section {
    padding: 1.5rem;
  }
}

/* ========================================
   ADVERTISER DASHBOARD MODAL STYLES
   ======================================== */

/* Create Ad Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
  backdrop-filter: blur(8px);
}

.modal-content {
  background: white;
  border-radius: 20px;
  max-width: 600px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(245, 113, 161, 0.1);
}

.modal-header {
  padding: 1.5rem 2rem;
  border-bottom: 1px solid #e2e8f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
  border-radius: 20px 20px 0 0;
}

.modal-header h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0;
}

.modal-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #64748b;
  padding: 0.5rem;
  border-radius: 50%;
  transition: all 0.3s ease;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-close:hover {
  background: rgba(245, 113, 161, 0.1);
  color: #F571A1;
}

.modal-body {
  padding: 2rem;
}

/* Form Styles */
.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  font-weight: 600;
  color: #374151;
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 0.875rem 1rem;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: white;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #F571A1;
  box-shadow: 0 0 0 3px rgba(245, 113, 161, 0.1);
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

@media (max-width: 640px) {
  .form-row {
    grid-template-columns: 1fr;
  }
}

/* Checkbox Groups */
.checkbox-group {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 0.75rem;
  margin-top: 0.5rem;
}

.checkbox-option {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.checkbox-option:hover {
  background: rgba(245, 113, 161, 0.05);
}

.checkbox-option input[type="checkbox"] {
  width: auto;
  margin: 0;
}

.checkbox-option span {
  font-size: 0.9rem;
  color: #374151;
  font-weight: 500;
}

/* Modal Actions */
.modal-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid #e2e8f0;
}

.btn-primary,
.btn-secondary {
  padding: 0.875rem 1.75rem;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 120px;
}

.btn-primary {
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(245, 113, 161, 0.3);
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(245, 113, 161, 0.4);
}

.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-secondary {
  background: #f1f5f9;
  color: #64748b;
  border: 1px solid #e2e8f0;
}

.btn-secondary:hover {
  background: #e2e8f0;
  color: #475569;
}

/* Payment Section */
.payment-section {
  padding: 1rem 0;
}

.payment-header {
  text-align: center;
  margin-bottom: 2rem;
}

.payment-header h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0 0 0.5rem 0;
}

.payment-header p {
  color: #64748b;
  margin: 0;
}

.payment-summary {
  background: #f8fafc;
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 2rem;
}

.summary-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
  border-bottom: 1px solid #e2e8f0;
}

.summary-item:last-child {
  border-bottom: none;
}

.summary-item.total {
  font-weight: 700;
  font-size: 1.1rem;
  color: #F571A1;
}

.payment-methods h4 {
  font-size: 1.1rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 1rem 0;
}

.payment-options {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.payment-option {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.payment-option:hover {
  border-color: #F571A1;
  background: rgba(245, 113, 161, 0.05);
}

.payment-option input[type="radio"] {
  margin: 0;
}

.option-content {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  color: #374151;
}

.option-content svg {
  color: #F571A1;
}

/* Campaign Details Modal */
.campaign-details-modal .modal-content {
