  display: flex;
  align-items: center;
  justify-content: center;
  color: #F571A1;
}

.metric-content {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.metric-value {
  font-size: 1.1rem;
  font-weight: 700;
  color: #1e293b;
}

.metric-label {
  font-size: 0.85rem;
  color: #64748b;
