  border-radius: 6px;
}

/* Ads Gallery */
.ads-gallery {
  min-height: 100vh;
  background: linear-gradient(135deg, #fff5f7 0%, #ffeef2 100%);
  padding: 2rem 1.5rem;
}

/* Header Section */
.gallery-header {
  text-align: center;
  margin-bottom: 3rem;
  background: white;
  border-radius: 24px;
  padding: 2.5rem;
  box-shadow: 0 8px 32px rgba(245, 113, 161, 0.1);
  position: relative;
  overflow: hidden;
}

.gallery-header::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(245, 113, 161, 0.05) 0%, transparent 70%);
  animation: float 8s ease-in-out infinite;
}

.header-content h1 {
  font-size: 2.5rem;
  font-weight: 800;
  color: #2d3748;
  margin: 0 0 1rem 0;
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.header-content p {
  font-size: 1.1rem;
  color: #718096;
  margin: 0;
  font-weight: 500;
  line-height: 1.6;
}

/* Filter Tabs */
.filter-tabs {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
  flex-wrap: wrap;
}

.filter-tab {
  padding: 0.875rem 1.5rem;
  border: 2px solid rgba(245, 113, 161, 0.2);
  background: white;
  color: #F571A1;
  border-radius: 25px;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.filter-tab::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(245, 113, 161, 0.1), transparent);
  transition: left 0.6s ease;
}

.filter-tab:hover::before {
  left: 100%;
}

.filter-tab:hover {
  border-color: #F571A1;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(245, 113, 161, 0.2);
}

.filter-tab.active {
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  color: white;
  border-color: #F571A1;
  box-shadow: 0 4px 15px rgba(245, 113, 161, 0.3);
}

