  border: 2px solid rgba(245, 113, 161, 0.15);
  box-shadow: 0 8px 25px rgba(245, 113, 161, 0.1);
}

.action-icon {
  font-size: 1.5rem;
}

.action-text {
  font-size: 1rem;
}

/* Section Styles */
.interests-section,
.why-ad-section,
.quick-stats-section,
.ad-actions,
.earnings-info {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.98) 0%, rgba(255, 245, 247, 0.98) 100%);
  backdrop-filter: blur(20px);
  padding: 2.5rem;
  border-radius: 30px;
  box-shadow: 0 20px 60px rgba(245, 113, 161, 0.12);
  border: 2px solid rgba(245, 113, 161, 0.08);
  margin-bottom: 2rem;
}

.section-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #333;
  margin: 0 0 1.5rem 0;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.section-icon {
  font-size: 1.8rem;
}

/* Interests Section */
.hashtags-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.hashtag {
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 25px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(245, 113, 161, 0.3);
}

.hashtag:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(245, 113, 161, 0.4);
}

/* Why This Ad Section */
.why-ad-section {
  background: linear-gradient(135deg, rgba(245, 113, 161, 0.08) 0%, rgba(255, 143, 171, 0.08) 50%, rgba(255, 179, 209, 0.08) 100%);
  border: 2px solid rgba(245, 113, 161, 0.15);
}

.why-ad-content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.why-ad-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 245, 247, 0.9) 100%);
  border-radius: 20px;
  transition: all 0.3s ease;
  border: 1px solid rgba(245, 113, 161, 0.1);
}

.why-ad-item:hover {
  background: linear-gradient(135deg, rgba(255, 255, 255, 1) 0%, rgba(255, 245, 247, 1) 100%);
  transform: translateX(5px);
  box-shadow: 0 8px 25px rgba(245, 113, 161, 0.15);
}

.why-ad-icon {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  color: white;
  box-shadow: 0 4px 15px rgba(245, 113, 161, 0.3);
}

.why-ad-item span {
  color: #333;
  font-weight: 500;
  font-size: 1rem;
}

/* Quick Stats */
.quick-stats-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.stat-card {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 245, 247, 0.9) 100%);
  padding: 1.5rem;
  border-radius: 25px;
  text-align: center;
  border: 2px solid rgba(245, 113, 161, 0.1);
  transition: all 0.3s ease;
  cursor: pointer;
  box-shadow: 0 8px 25px rgba(245, 113, 161, 0.08);
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 50px rgba(245, 113, 161, 0.15);
  background: linear-gradient(135deg, rgba(255, 255, 255, 1) 0%, rgba(255, 245, 247, 1) 100%);
  border-color: rgba(245, 113, 161, 0.2);
}

.stat-card-icon {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.stat-card-value {
  font-size: 1.5rem;
  font-weight: 800;
  color: #333;
  margin-bottom: 0.5rem;
}

.stat-card-label {
  font-size: 0.8rem;
  color: #666;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
}

/* Action Buttons */
.ad-actions {
  display: flex;
  gap: 1.5rem;
  flex-wrap: wrap;
}

.action-btn.primary {
  flex: 1;
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  color: white;
  border: none;
  padding: 1.25rem 2rem;
  border-radius: 25px;
  cursor: pointer;
  font-size: 1.1rem;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(245, 113, 161, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  min-width: 200px;
}

.action-btn.primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(245, 113, 161, 0.4);
}

.action-btn-icon {
  font-size: 1.5rem;
}

.action-btn-text {
  font-weight: 600;
}

.share-btn-secondary {
  background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
  color: white;
  border: none;
  padding: 1.25rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(40, 167, 69, 0.3);
  display: flex;
  align-items: center;
  gap: 0.75rem;
  white-space: nowrap;
}

.share-btn-secondary:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(40, 167, 69, 0.4);
}

.share-icon {
  font-size: 1.3rem;
}

.share-text {
  font-weight: 600;
}

.earnings-badge {
  background: rgba(255, 255, 255, 0.2);
  padding: 0.5rem 1rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 800;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

/* Earnings Info */
.earnings-info {
  background: linear-gradient(135deg, rgba(245, 113, 161, 0.08) 0%, rgba(255, 143, 171, 0.08) 50%, rgba(255, 179, 209, 0.08) 100%);
  border: 2px solid rgba(245, 113, 161, 0.15);
}

.earnings-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.earnings-icon {
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: white;
  box-shadow: 0 8px 25px rgba(245, 113, 161, 0.3);
}

.earnings-title {
  font-size: 1.4rem;
  font-weight: 700;
  color: #F571A1;
  margin: 0;
}

.earnings-description {
  color: #666;
  line-height: 1.7;
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.earnings-features {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.feature {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: #666;
  font-weight: 500;
  font-size: 1rem;
}

.feature-icon {
  color: #F571A1;
  font-size: 1.2rem;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .content-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .image-section {
    position: static;
  }
  
  .page-header {
    flex-direction: column;
    text-align: center;
    gap: 1.5rem;
    padding: 1.5rem;
  }
  
  .page-title {
    order: 2;
    font-size: 1.8rem;
  }
  
  .back-btn {
    order: 1;
    align-self: flex-start;
  }
  
  .share-btn.header-share {
    order: 3;
    align-self: center;
  }
  
  .ad-details-content {
    padding: 0 1rem;
  }
}

@media (max-width: 768px) {
  .ad-details-page {
    padding: 0;
  }
  
  .page-header {
    border-radius: 0 0 20px 20px;
    margin-bottom: 1rem;
    padding: 1rem;
  }
  
  .page-title {
    font-size: 1.5rem;
  }
  
  .details-section,
  .interests-section,
  .why-ad-section,
  .quick-stats-section,
  .ad-actions,
  .earnings-info {
    padding: 1.5rem;
    border-radius: 20px;
  }
  
  .ad-title {
    font-size: 1.8rem;
  }
  
  .ad-actions {
    flex-direction: column;
  }
  
  .action-btn.primary,
  .share-btn-secondary {
    width: 100%;
    min-width: auto;
  }
  
  .quick-stats-grid {
    grid-template-columns: 1fr;
  }
  
  .image-stats {
    gap: 2rem;
  }
  
  .earnings-info {
    padding: 1.5rem;
  }
  
  .ad-details-content {
    padding: 0 0.5rem;
  }
}

@media (max-width: 480px) {
  .page-header {
    padding: 1rem;
  }
  
  .details-section,
  .interests-section,
  .why-ad-section,
  .quick-stats-section,
  .ad-actions,
  .earnings-info {
    padding: 1rem;
  }
  
  .ad-title {
    font-size: 1.5rem;
  }
  
  .image-overlay {
    padding: 1.5rem;
  }
  
  .image-stats {
    flex-direction: column;
    gap: 1rem;
  }
  
  .hashtags-container {
    gap: 0.75rem;
  }
  
  .hashtag {
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
  }
}

/* ========================================
   BEAUTIFUL LOGIN PAGES - Pink & White Theme
   ======================================== */

/* Main Login Container */
.login-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #fff5f7 0%, #ffeef2 25%, #fce7f3 50%, #fdf2f8 75%, #ffffff 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem 1rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  overflow: hidden;
}

.login-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 300px;
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 50%, #ffb3d1 100%);
  opacity: 0.08;
  z-index: 0;
}

.login-content {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.98) 0%, rgba(255, 245, 247, 0.98) 100%);
  backdrop-filter: blur(20px);
  border-radius: 30px;
  padding: 3rem 2.5rem;
  box-shadow: 0 25px 70px rgba(245, 113, 161, 0.15);
  border: 2px solid rgba(245, 113, 161, 0.1);
  max-width: 450px;
  width: 100%;
  text-align: center;
  position: relative;
  z-index: 1;
}

/* Header Section */
.header-section {
  margin-bottom: 2rem;
}

.brand-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.brand-logo {
  width: 80px;
  height: 80px;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(245, 113, 161, 0.2);
  border: 3px solid rgba(245, 113, 161, 0.1);
}

.brand-name {
  font-size: 2rem;
  font-weight: 700;
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 0;
}

/* Illustration Section */
.illustration-section {
  margin-bottom: 2rem;
}

.dating-illustration {
  width: 200px;
  height: auto;
  border-radius: 20px;
  box-shadow: 0 15px 40px rgba(245, 113, 161, 0.15);
}

/* Slider Styles */
.slider-container {
  position: relative;
  width: 100%;
  overflow: hidden;
  margin-bottom: 2rem;
}

.slider-wrapper {
  position: relative;
  width: 100%;
  height: auto;
}

.slider-content {
  display: flex;
  transition: transform 0.5s ease-in-out;
  width: 100%;
}

.slide {
  width: 100%; /* Each slide takes full width of container */
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slide-content {
  text-align: center;
  margin-top: 1rem;
}

.slider-dots {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 1.5rem;
}

.slider-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background-color: rgba(245, 113, 161, 0.3);
  cursor: pointer;
  transition: all 0.3s ease;
}

.slider-dot.active {
  background-color: #F571A1;
  transform: scale(1.2);
}

.slider-dot:hover {
  background-color: rgba(245, 113, 161, 0.6);
}

/* Content Section */
.tagline-title {
  font-size: 1.8rem;
  font-weight: 700;
  color: #333;
  margin: 0 0 0.5rem 0;
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.tagline-subtitle {
  font-size: 1.1rem;
  color: #666;
  margin: 0 0 2rem 0;
  line-height: 1.6;
}

/* Form Section */
.form-section {
  margin-top: 2rem;
}

.message {
  padding: 1rem;
  border-radius: 15px;
  margin-bottom: 1.5rem;
  font-weight: 500;
}

.message.success {
  background: linear-gradient(135deg, rgba(76, 175, 80, 0.1) 0%, rgba(129, 199, 132, 0.1) 100%);
  color: #2e7d32;
  border: 2px solid rgba(76, 175, 80, 0.2);
}

.message.error {
  background: linear-gradient(135deg, rgba(244, 67, 54, 0.1) 0%, rgba(239, 154, 154, 0.1) 100%);
  color: #c62828;
  border: 2px solid rgba(244, 67, 54, 0.2);
}

.login-form {
  text-align: left;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-label {
  display: block;
  font-weight: 600;
  color: #333;
  margin-bottom: 0.5rem;
  font-size: 1rem;
}

.form-input {
  width: 100%;
  padding: 1rem 1.2rem;
  border: 2px solid rgba(245, 113, 161, 0.15);
  border-radius: 20px;
  font-size: 1.1rem;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 245, 247, 0.95) 100%);
  transition: all 0.3s ease;
  box-sizing: border-box;
  font-weight: 500;
  color: #333;
  position: relative;
  flex: 1;
}

.form-input:focus {
  outline: none;
  border-color: #F571A1;
  box-shadow: 0 0 0 4px rgba(245, 113, 161, 0.15), 0 8px 25px rgba(245, 113, 161, 0.1);
  background: white;
  transform: translateY(-1px);
}

.form-input::placeholder {
  color: #bbb;
  font-weight: 400;
}

/* Enhanced Mobile Number Input Container */
.mobile-input-container {
  position: relative;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 245, 247, 0.8) 100%);
  border-radius: 20px;
  padding: 0.75rem;
  border: 1px solid rgba(245, 113, 161, 0.08);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.mobile-input-container:hover {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 245, 247, 0.9) 100%);
  border-color: rgba(245, 113, 161, 0.15);
  transform: translateY(-1px);
  box-shadow: 0 5px 20px rgba(245, 113, 161, 0.08);
}

.mobile-input-container:focus-within {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 245, 247, 0.95) 100%);
  border-color: rgba(245, 113, 161, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(245, 113, 161, 0.12);
}

.mobile-input-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(245, 113, 161, 0.05) 0%, rgba(255, 143, 171, 0.05) 100%);
  border-radius: 20px;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.mobile-input-container:focus-within::before {
  opacity: 1;
}

/* Country Code Styling */
.country-code {
  position: relative;
  color: #F571A1;
  font-weight: 600;
  font-size: 1rem;
  background: linear-gradient(135deg, rgba(245, 113, 161, 0.1) 0%, rgba(255, 143, 171, 0.1) 100%);
  padding: 0.5rem 0.8rem;
  border-radius: 12px;
  border: 1px solid rgba(245, 113, 161, 0.2);
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(245, 113, 161, 0.1);
  flex-shrink: 0;
  min-width: 60px;
  text-align: center;
}

.mobile-input-container:hover .country-code {
  background: linear-gradient(135deg, rgba(245, 113, 161, 0.12) 0%, rgba(255, 143, 171, 0.12) 100%);
  border-color: rgba(245, 113, 161, 0.25);
  transform: scale(1.02);
  box-shadow: 0 3px 12px rgba(245, 113, 161, 0.15);
}

.mobile-input-container:focus-within .country-code {
  background: linear-gradient(135deg, rgba(245, 113, 161, 0.15) 0%, rgba(255, 143, 171, 0.15) 100%);
  border-color: rgba(245, 113, 161, 0.3);
  transform: scale(1.05);
  box-shadow: 0 4px 15px rgba(245, 113, 161, 0.2);
}

/* Helper Text Enhancement */
.helper-text {
  font-size: 0.9rem;
  color: #888;
  margin: 0.75rem 0 0 0;
  text-align: center;
  font-weight: 400;
  line-height: 1.4;
  transition: all 0.3s ease;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  background: rgba(245, 113, 161, 0.03);
  border: 1px solid rgba(245, 113, 161, 0.05);
}

.mobile-input-container:focus-within + .helper-text {
  color: #F571A1;
  font-weight: 500;
  background: rgba(245, 113, 161, 0.08);
  border-color: rgba(245, 113, 161, 0.15);
  transform: translateY(-1px);
}

/* Input Field Icon Animation - Removed since we have country code on left */

/* Floating Label Effect */
.form-label {
  display: block;
  font-weight: 600;
  color: #333;
  margin-bottom: 0.8rem;
  font-size: 1rem;
  transition: all 0.3s ease;
  position: relative;
}

.mobile-input-container:focus-within + .form-label,
.mobile-input-container:focus-within ~ .form-label {
  color: #F571A1;
  transform: translateY(-2px);
}

/* Primary Button */
.primary-btn {
  width: 100%;
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  color: white;
  border: none;
  border-radius: 15px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(245, 113, 161, 0.3);
  margin-top: 1rem;
}

.primary-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(245, 113, 161, 0.4);
}

.primary-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

/* Terms Section */
.terms-section {
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid rgba(245, 113, 161, 0.1);
}

.terms-text {
  font-size: 0.9rem;
  color: #666;
  line-height: 1.5;
  margin: 0;
}

.terms-link {
  color: #F571A1;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.terms-link:hover {
  color: #ff8fab;
  text-decoration: underline;
}

