.admin-login-btn {
  width: 100%;
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #F571A1 0%, #ff8fab 100%);
  color: white;
  border: none;
  border-radius: 15px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
