  box-shadow: 0 8px 20px rgba(255, 107, 157, 0.3);
}

.modal-actions .primary-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.modal-actions .secondary-btn {
  background: #f8f9fa;
  color: #666;
  border: 2px solid #e0e0e0;
}

.modal-actions .secondary-btn:hover:not(:disabled) {
  background: #e9ecef;
  border-color: #d0d0d0;
}

.step-progress {
  padding: 20px 25px;
  border-top: 1px solid #f0f0f0;
  text-align: center;
}

.progress-dots {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 10px;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #e0e0e0;
  transition: background-color 0.3s ease;
}

.dot.active {
  background: #ff6b9d;
}

.dot.completed {
  background: #28a745;
}

.progress-text {
  font-size: 0.8rem;
  color: #666;
  font-weight: 500;
}

/* Mobile responsiveness for modals */
@media (max-width: 480px) {
  .modal-overlay {
    padding: 10px;
  }
  
  .step-registration-modal {
    border-radius: 15px;
  }
  
  .modal-header,
  .step-form {
    padding: 20px;
  }
  
  .interests-grid {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 8px;
  }
  
  .suggestions-grid {
    grid-template-columns: repeat(auto-fit, minmax(70px, 1fr));
    gap: 6px;
  }
  
  .modal-actions {
    flex-direction: column;
  }
  
  .modal-actions .primary-btn,
  .modal-actions .secondary-btn {
    width: 100%;
  }
}