.bottom-navigation {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: #ffffff;
  backdrop-filter: blur(10px);
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 8px 0;
  z-index: 1000;
  box-shadow: 0 -2px 20px rgba(0, 0, 0, 0.1);
  height: 70px;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  color: rgba(0, 0, 0, 0.6);
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 6px 8px;
  border-radius: 12px;
  min-width: 50px;
  position: relative;
}

.nav-item:hover {
  color: rgba(0, 0, 0, 0.8);
  transform: translateY(-2px);
}

.nav-item.active {
  color: #667eea;
  background: rgba(102, 126, 234, 0.1);
  transform: translateY(-2px);
}

.nav-item.active::before {
  content: '';
  position: absolute;
  top: -2px;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: 4px;
  background: #667eea;
  border-radius: 50%;
  box-shadow: 0 0 8px rgba(102, 126, 234, 0.5);
}

.nav-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 4px;
  transition: all 0.3s ease;
}

.nav-item.active .nav-icon {
  transform: scale(1.1);
}

.nav-label {
  font-size: 10px;
  font-weight: 500;
  text-align: center;
  line-height: 1;
  transition: all 0.3s ease;
}

.nav-item.active .nav-label {
  font-weight: 600;
}

/* Add padding to pages to account for bottom navigation */
.page-with-bottom-nav {
  padding-bottom: 80px;
}

/* Responsive adjustments */
@media (max-width: 480px) {
  .bottom-navigation {
    height: 65px;
    padding: 6px 0;
  }
  
  .nav-item {
    padding: 4px 6px;
    min-width: 45px;
  }
  
  .nav-label {
    font-size: 9px;
  }
  
  .page-with-bottom-nav {
    padding-bottom: 75px;
  }
}

/* Animation for nav items */
@keyframes navItemBounce {
  0% { transform: translateY(0); }
  50% { transform: translateY(-3px); }
  100% { transform: translateY(0); }
}

.nav-item.active {
  animation: navItemBounce 0.6s ease-in-out;
}

/* Ripple effect on click */
.nav-item {
  position: relative;
  overflow: hidden;
}

.nav-item::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(102, 126, 234, 0.2);
  transform: translate(-50%, -50%);
  transition: width 0.3s ease, height 0.3s ease;
}

.nav-item:active::after {
  width: 100px;
  height: 100px;
}
